/* 
Pretty, isn't it?
*/
$pretty--class-name: pretty !default;
$pretty--color-default: #bdc3c7 !default;
$pretty--color-error: #f95e5e !default;
$pretty--color-box-bg: $pretty--color-default !default;
$pretty--color-box-border: $pretty--color-default !default;
$pretty--radius-box: 0% !default;
$pretty--box-width: 1em;
$pretty--box-height: 1em;
$pretty--box-size: calc(1em + 2px);
$pretty--color-primary: #428bca !default;
$pretty--color-info: #5bc0de !default;
$pretty--color-success: #5cb85c !default;
$pretty--color-warning: #f0ad4e !default;
$pretty--color-danger: #d9534f !default;
$pretty--color-dark: #5a656b !default;
$pretty--z-index-back: 0;
$pretty--z-index-between: 1;
$pretty--z-index-front: 2;
$pretty--label-text-offset: 1.5em;
$pretty--label-text-offset-switch: 2.5em;
$pretty-top-offset-old: calc(calc(50% - calc(100% - 1em)) - 1px);
$pretty-top: 8;
$pretty-top-switch: ($pretty-top*2)*1%;
$pretty-top-offset: calc(calc(0% - calc(100% - 1em)) - #{$pretty-top * 1%});
$pretty-top-offset-switch: calc(calc(0% - calc(100% - 1em)) - #{$pretty-top-switch});
$pretty--sets: (primary, $pretty--color-primary),
(info, $pretty--color-info),
(success, $pretty--color-success),
(warning, $pretty--color-warning),
(danger, $pretty--color-danger);
$pretty--env: 'PROD';
$pretty--dev-err: 'Invalid input type!';
$pretty--scale-bigger: 1.2em;


/// Z-indexes map, gathering all Z layers of the application
/// @access private
/// @type Map
/// @prop {String} key - Layer’s name
/// @prop {Number} value - Z value mapped to the key
/// @type Length